import React, {useState} from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import {TAB_NAMES} from '../../config';
import styles from './styles.scss';

const PAGINATED_ITEM_WIDTH = 134;
const ITEMS_PER_PAGE = 5;
const PAGE_OFFSET = PAGINATED_ITEM_WIDTH * ITEMS_PER_PAGE;

export const TabSelector = ({adblockers, currentTab, onSelectTab}) => {
  const [page, setPage] = useState(0);
  const isPaginated = adblockers.length > 6;

  const tabs = adblockers.map((adBlocker) =>
    <div
      className={classnames(styles[adBlocker], styles.tab, currentTab === adBlocker && styles.selected)}
      key={adBlocker}
      onClick={() => onSelectTab(adBlocker)}
    >
      {TAB_NAMES[adBlocker]}
    </div>
  );

  if (isPaginated) {
    const maxPage = Math.ceil(adblockers.length / ITEMS_PER_PAGE) - 1;
    const onPageLeft = () => {
      if (page > 0) {
        setPage(page - 1);
      }
    };
    const onPageRight = () => {
      if (page < maxPage) {
        setPage(page + 1);
      }
    };
    let leftOffset = -(PAGE_OFFSET * page);
    const perfectFitItemCount = ITEMS_PER_PAGE * (maxPage + 1) + 1;

    if (page === maxPage && adblockers.length < perfectFitItemCount) {
      leftOffset = -(PAGE_OFFSET * page) + (perfectFitItemCount - adblockers.length) * PAGINATED_ITEM_WIDTH;
    }

    const scrollerStyle = {
      left: leftOffset
    };

    return (
      <div className={styles.paginated}>
        <button className={styles.pageLeft} disabled={page < 1} onClick={onPageLeft} />
        <div className={styles.scrollContainer}>
          <div className={styles.scroller} style={scrollerStyle}>
            {tabs}
          </div>
        </div>
        <button className={styles.pageRight} disabled={page >= maxPage} onClick={onPageRight} />
      </div>
    );
  }

  return (
    <div className={styles.notPaginated}>
      {tabs}
    </div>
  );
};

TabSelector.propTypes = {
  adblockers: PropTypes.arrayOf(PropTypes.string).isRequired,
  currentTab: PropTypes.string.isRequired,
  onSelectTab: PropTypes.func.isRequired
};



