import React from 'react';
import PropTypes from 'prop-types';
import {BROWSER_NAMES, BROWSER_LINKS, TAB_KEYS_BY_BROWSER} from '../../config';
import styles from './styles.scss';

export const BottomLinks = ({browser, onNotSure}) => {
  const showHelpButton = TAB_KEYS_BY_BROWSER[browser].includes('other');

  return (
    <div className={styles.bottomLinks}>
      {showHelpButton && <div className={styles.notSure}>
        Not sure which ad blocker you have? <button className={styles.notSureButton} onClick={onNotSure}>Click here</button>
      </div>}
      {!showHelpButton && <div className={styles.notSure} />}
      <a className={styles.browserHelp} href={BROWSER_LINKS[browser]} rel='noopener nofollow noreferrer' target='_blank'>
        Get help on {BROWSER_NAMES[browser]}
      </a>
    </div>
  );
};

BottomLinks.propTypes = {
  browser: PropTypes.string.isRequired,
  onNotSure: PropTypes.func.isRequired
};



