import React, {useState, useEffect} from 'react';
import classnames from 'classnames';
import styles from './styles.scss';

const FADEOUT_ANIMATION_TIME = 500;
const TIME_TO_AUTOCLOSE = 5000;

export const Thanks = () => {
  const [state, setState] = useState('open');
  let startCloseTimeout = null;
  let endCloseTimeout = null;

  const handleEndClose = () => {
    if (startCloseTimeout) {
      clearTimeout(startCloseTimeout);
      startCloseTimeout = null;
    }
    if (endCloseTimeout) {
      clearTimeout(endCloseTimeout);
      endCloseTimeout = null;
    }
    setState('closed');
  };

  const handleStartClose = () => {
    if (state === 'open') {
      setState('closing');
      endCloseTimeout = setTimeout(handleEndClose, FADEOUT_ANIMATION_TIME);
    }
  };

  useEffect(() => {
    startCloseTimeout = setTimeout(handleStartClose, TIME_TO_AUTOCLOSE);
  }, []);

  if (state === 'closed') {
    return null;
  }

  return (
    <div className={classnames(styles.thanks, state === 'closing' && styles.closing)}>
      Thanks for disabling your ad blocker
      <button className={styles.closeButton} onClick={handleEndClose} />
    </div>
  );
};



