import React from 'react';
import PropTypes from 'prop-types';
import {sourcesToAttributes} from '../../retina';
import {DisableHelp} from '../DisableHelp';
import {ADBLOCKER_CONTENT} from './content';
import styles from './styles.scss';

export const TabContent = ({browser, currentTab}) => {
  const contentData = ADBLOCKER_CONTENT[currentTab] && (ADBLOCKER_CONTENT[currentTab][browser] || ADBLOCKER_CONTENT[currentTab].default);

  if (contentData) {
    return (
      <div className={styles.container}>
        <div className={styles.steps}>
          {contentData.steps.map((stepText, idx) =>
            <div className={styles.step} key={idx}>
              <div className={styles.stepLabel}>Step {idx + 1}:</div>
              <div className={styles.stepText}>{stepText}</div>
            </div>
          )}
        </div>
        {contentData.image &&
          <div className={styles.image}>
            <img alt='screenshot' {...sourcesToAttributes(contentData.image)} />
          </div>
        }
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <DisableHelp browser={browser} />
    </div>
  );
};

TabContent.propTypes = {
  browser: PropTypes.string.isRequired,
  currentTab: PropTypes.string.isRequired
};



