import React from 'react';
import {detect} from 'detect-browser';
import {render} from 'react-dom';
import {Main} from './components/Main';
import {Thanks} from './components/Thanks';

export const init = (forceBrowser) => {
  const criteria = window.PageCriteria || {};

  const browser = detect();

  const element = document.createElement('div');

  document.body.append(element);
  document.body.classList.add('mol-fe-ab-dialog');

  render(<Main browser={forceBrowser || browser && browser.name || 'unknown'} geo={criteria.geo || 'GB'} />, element);
};

export const thanks = () => {
  const element = document.createElement('div');

  document.body.append(element);

  render(<Thanks />, element);
};



