import React from 'react';
import PropTypes from 'prop-types';
import {TAB_KEYS_BY_BROWSER} from '../../config';
import styles from './styles.scss';

export const IntroINEWS = ({browser, onViewGuide}) => {
  const hasBrowserTabs = TAB_KEYS_BY_BROWSER[browser] && TAB_KEYS_BY_BROWSER[browser].length;

  const {container, heading, text, viewGuide} = styles;

  return (
    <div className={`${container}`}>
      <div className={heading}>We see that you&apos;re using an<br />ad blocker</div>
      <div className={text}>
        Advertising supports our non-partisan, quality journalism.<br />
        To help our journalists bring you all the stories you enjoy, please allow ads on this site.
      </div>
      {hasBrowserTabs && <div className={text}>
        Need help disabling your ad blocker? <button className={viewGuide} onClick={onViewGuide}>View our guide</button>
      </div>}
    </div>
  );
};

IntroINEWS.propTypes = {
  browser: PropTypes.string.isRequired,
  geo: PropTypes.string.isRequired,
  onViewGuide: PropTypes.func.isRequired
};



