import React, {useState} from 'react';
import PropTypes from 'prop-types';
import {TAB_KEYS_BY_BROWSER} from '../../config';
import {BottomLinks} from '../BottomLinks';
import {TabSelector} from '../TabSelector';
import {TabContent} from '../TabContent';
import styles from './styles.scss';

export const Guide = ({browser}) => {
  const browserTabs = TAB_KEYS_BY_BROWSER[browser];
  const [currentTab, setCurrentTab] = useState(browserTabs && browserTabs[0]);

  if (!browserTabs) {
    return null;
  }

  return (
    <div className={styles.container}>
      {currentTab !== 'other' && <div className={styles.heading}>Please disable ad blocker for inews.co.uk to get unlimited access to this site</div>}
      <TabSelector adblockers={TAB_KEYS_BY_BROWSER[browser]} currentTab={currentTab} onSelectTab={setCurrentTab} />
      <TabContent browser={browser} currentTab={currentTab} />
      <BottomLinks browser={browser} onNotSure={() => setCurrentTab('other')} />
    </div>
  );
};

Guide.propTypes = {
  browser: PropTypes.string.isRequired
};



