import React from 'react';
import PropTypes from 'prop-types';
import {IntroINEWS} from '../IntroINEWS';

// eslint-disable-next-line no-confusing-arrow
export const Intro = ({browser, geo, onViewGuide}) => <IntroINEWS browser={browser} geo={geo} onViewGuide={onViewGuide} />;

Intro.propTypes = {
  browser: PropTypes.string.isRequired,
  geo: PropTypes.string.isRequired,
  onViewGuide: PropTypes.func.isRequired
};



