import React from 'react';
import PropTypes from 'prop-types';
import {sourcesToAttributes} from '../../retina';
import * as content from './content';
import styles from './styles.scss';

const contentToSteps = (steps) => steps.map((stepItems, stepIdx) =>
  <div className={styles.step} key={stepIdx}>
    <div className={styles.stepLabel}>Step {stepIdx + 1}:</div>
    {stepItems.map((stepItem, stepItemIdx) => {
      if (typeof stepItem === 'string') {
        return <div className={styles.stepText} key={`${stepIdx}.${stepItemIdx}`}>{stepItem}</div>;
      } else if (stepItem.src) {
        return <img className={styles.stepImage} key={`${stepIdx}.${stepItemIdx}`} {...sourcesToAttributes(stepItem.src)} />;
      }

      return null;
    })}
  </div>
);

export const DisableHelp = ({browser}) =>
  <div className={styles.container}>
    <div className={styles.heading}>What ad blocker am I using and how do I disable it?</div>
    <div className={styles.columns}>
      <div className={content[browser].right ? styles.column : styles.singleColumn}>
        <div className={styles.columnTitle}>Find your ad blocker and remove, or disable it for all sites</div>
        <div className={styles.columnContent}>
          {contentToSteps(content[browser].left)}
        </div>
      </div>
      {content[browser].right && <div className={styles.column}>
        <div className={styles.columnTitle}>Find your ad blocker and disable it for iNews only</div>
        <div className={styles.columnContent}>
          {contentToSteps(content[browser].right)}
        </div>
      </div>}
    </div>
  </div>;

DisableHelp.propTypes = {
  browser: PropTypes.string.isRequired
};



